.footer h5 {
    font-size: 1.1rem;
    color: #fff;
}

.footer {
    padding-bottom: 30px;
    background: #000;
}

.list__item {
    background: transparent !important;
    padding-left: 0;
    border: none !important ;
}

.list__item a{
    text-decoration: none;
    color: #fff;
    opacity: 0.8;
    font-size: 0.9rem;

}

.list__item a:hover {
    color: #fff;
    opacity: 0.8;
}

.newsletters {
    width: 100%;
    border-radius: 50px;
    color: #fff;
    padding: 8px 25px;
    margin-top: 20px;
    border: none;
    outline: none;
    font-size: 0.8rem;
    background: #343444;
}

::placeholder {
    color: #fff;
    opacity: 0.8;
}

.social__links span a{
    text-decoration: none;
    color: #fff;
    opacity: 0.8;
    font-weight: 400;
    font-size: 1.2rem;
    transition: 0.3s;
}

.social__links span a:hover {
    color: #e250e5;
}

.copyright {
    font-size: 0.8rem;
    opacity: 0.8;
}