.modal__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #14141f8a;
    z-index: 4444;
}

.single__modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 500px;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    padding: 30px;
    z-index: 5555;
    background: #343444;
    cursor: pointer;
}

.single__modal h6 {
    font-size: 1.1rem;
    font-weight: 500;
    margin-bottom: 10px;
}

.single__modal .money {
    color: #fff;
    font-weight: 500;
    font-size: 1.1rem;

}

.input__item input {
    width: 100%;
    border: 1px solid rgba(221, 221, 221, 0.274);
    color: #fff;
    padding: 8px 25px;
    outline: none;
    background: transparent;
    font-size: 0.8rem;
}

.input__item h6 {
    font-size: .9rem;
    color: #fff;
}

.place__bid-btn {
    padding: 7px 25px;
    border: none;
    outline: none;
    width: 120px;
    margin: auto;
    background: #5142FC;
    color: #fff;
    border-radius: 50px;
    font-size: 0.8rem;
    display: block;
    margin-top: 15px;
}

.close__modal {
    position: absolute;
    top: -5px;
    right: -5px;
    z-index: 6666;
}

.close__modal i {
    padding: 5px;
    background: #5142fc;
    color: #fff;
    border-radius: 50%;
}


@media only screen and (max-width:992px) {
    .single__modal {
        width: 300px;
        height: auto;
    }

    .single__modal h6 {
        font-size: .8rem;
    }

    .single__modal p {
        font-size: .8rem;
        margin-bottom: 0;
    }

    .money {
        font-size: 0.8rem !important;
    }

    .input__item h6 {
        font-size: .7rem;
    }

    .input__item input {
        padding: 5px 20px;
        font-size: 0.7rem !important;
    }

}