@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,700;1,600&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;


}

body {
    background: #14141f !important;
    font-family: "Roboto", sans-serif;

}

h1,h2 {
    color: #fff;
    font-weight: 500;
    font-size: 2rem;

}

p {
    font-weight: 400;
    font-size: 1rem;
    color: #fff;
    line-height: 35px;
}

a {
    text-decoration: none;
    color: unset;
}

a:hover {
    color: unset;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

section,footer {
    padding: 60px 0px;
}

/*========== custom scroll-bar ============ */

::-webkit-scrollbar {
    width: 10px;
    background: #343444;

}

::-webkit-scrollbar-thumb {
    border-radius: 2em;
    background: linear-gradient(-45deg, #E250E5, #4B50E6, #E250E5,#4B50E6);;
}


.hero__section {
    margin-top: 120px;
}

.hero__section h2 {
    line-height: 55px;
    font-size: 2.5rem;
}


.hero__content h2 span {
    background:linear-gradient(-45deg, #E250E5, #4B50E6, #E250E5,#4B50E6);
    background-size: 100% 100%;
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-stroke: 3px transparent;
    -webkit-text-fill-color: #14141f;
    margin-left: 7px !important;
    display: inline-block;
}

.hero__btns {
    margin-top: 40px;

}

.hero__btns .explore__btn,.create__btn {
    border: none;
    outline: none;
    padding: 7px 25px;
    border-radius: 50px !important;
    border: 1px solid #5142FC !important;
    font-size: 0.9rem;
    background: transparent;

}

.explore__btn i,
.create__btn i {
    color: #fff;
    font-size: 1rem;
}

.explore__btn a,
.create__btn a {
    text-decoration: none ;
    color: #fff;
}

.hero__btns button a:hover {
    color: #fff;
}

.hero__img {
    width: 500px;
    margin-left: auto;
}

.hero__img img {
    border-radius: 7px;
}


@media only screen and (max-width:992px) {

    .hero__section {
        margin-top: 90px;
    }
    .hero__img {
        width: 300px;
    }

    .hero__content h2 {
        font-size: 1.5rem;
        line-height: 40px;

    }

    .hero__content p {
        font-size: 0.8rem;
        line-height: 25px;
    }

    .create__btn, .explore__btn {
        padding: 4px 25px !important;
    }

    .create__btn a, .explore__btn a {
        font-size: 0.7rem;
    }

    
}

@media only screen and (max-width:576px) {
    .hero__img {
        width: 100%;
        margin: auto;
        margin-top: 30px;
    }
}


.modal__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #14141f8a;
    z-index: 4444;
}

.single__modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 500px;
    transform: translate(-50%, -50%);
    border-radius: 5px;
    padding: 30px;
    z-index: 5555;
    background: #343444;
    cursor: pointer;
}

.single__modal h6 {
    font-size: 1.1rem;
    font-weight: 500;
    margin-bottom: 10px;
}

.single__modal .money {
    color: #fff;
    font-weight: 500;
    font-size: 1.1rem;

}

.input__item input {
    width: 100%;
    border: 1px solid rgba(221, 221, 221, 0.274);
    color: #fff;
    padding: 8px 25px;
    outline: none;
    background: transparent;
    font-size: 0.8rem;
}

.input__item h6 {
    font-size: .9rem;
    color: #fff;
}

.place__bid-btn {
    padding: 7px 25px;
    border: none;
    outline: none;
    width: 120px;
    margin: auto;
    background: #5142FC;
    color: #fff;
    border-radius: 50px;
    font-size: 0.8rem;
    display: block;
    margin-top: 15px;
}

.close__modal {
    position: absolute;
    top: -5px;
    right: -5px;
    z-index: 6666;
}

.close__modal i {
    padding: 5px;
    background: #5142fc;
    color: #fff;
    border-radius: 50%;
}


@media only screen and (max-width:992px) {
    .single__modal {
        width: 300px;
        height: auto;
    }

    .single__modal h6 {
        font-size: .8rem;
    }

    .single__modal p {
        font-size: .8rem;
        margin-bottom: 0;
    }

    .money {
        font-size: 0.8rem !important;
    }

    .input__item h6 {
        font-size: .7rem;
    }

    .input__item input {
        padding: 5px 20px;
        font-size: 0.7rem !important;
    }

}


@media only screen and (max-width:992px) {
    .nft__title {
        font-size: 0.9rem !important; 
    }

    .creator__info h6 {
        font-size: .6rem !important;
    }

    .creator__info p {
        font-size: .7rem !important;
    }

    .bid_btn {
        padding: 4px 20px !important;
        font-size: 0.7rem !important;
    }

    .history__link a{
        font-size: 0.6rem !important;
    }

}


.single__nft__card {
    padding: 20px;
    background: #343444;

}

.nft__img img {
    border-radius: 10px;

}

.nft__img {
margin-bottom: 10px;
}

.nft__content .nft__title {
    color: #fff !important;
    font-size: 0.9rem;
    margin-bottom: 15px;
}

.nft__title a {
    color: #fff !important;
    font-size: 0.9rem;
    text-decoration: none;
}

.creator__img {
    width: 40px;
    height: 40px;
}

.creator__info h6 {
    font-size: 0.7rem;
    color: #fff;
    opacity: 0.8;

    margin-bottom: 0;
}

.creator__info p {
    font-size: .9rem;
    color: #fff;
    opacity: 1;
    font-weight: 500;
    margin-bottom: 0;
}


.bid__btn {
    border:none;
    outline: none;
    padding: 5px 22px;
    background: #5142fc;
    border: 1px solid #5142fc;
    font-size: .9rem;
    color: #fff;
    border-radius: 50px;
}

.bid__btn:hover {
    background: #fff;
    color: #5142fc;
}

.history__link a {
    text-decoration: none;
    color: #fff;
    opacity: 0.8;
    font-size: .9rem;
}

.history__link a:hover {
    color: #fff;
}


.live__auction__top h3 {
    color: #fff;
}

.live__auction__top span a {
    text-decoration: none;
    color: #fff;
    border-bottom: 1px solid #5142fc;
}
.seller__section-title h3{
    color: #fff;
}

.seller__img {
    width: 80px;
    height: 80px;
}

.seller__content h6 {
    font-size: 1rem;
    color: #fff;
    
}

@media only screen and (max-width:992px) {
    .seller__content h6 {
        font-size: 0.8rem;
    }
}
.trending__title  {
    color: #fff;
}
.single__step__item {
    background: #343444;
    border-radius: 5px;
    padding: 30px;
    /* text-align: center; */
}

.step__title {
    color: #fff;

}

.step__item__content h5 a {
    text-decoration: none;
    color: #fff;
}

.single__step__item span i {
    color: #fff;
    padding: 20px;
    font-size: 1.4rem;
    background: #5142FC;

}

.single__step__item span {
    margin-bottom: 15px;
    border-radius: 5px;
}
.step__item__content {
    margin-top: 20px;
}

@media only screen and (max-width:992px) {
    .step__itm__content h5 a {
        font-size: 1rem;

    }

    .single__step__item p {
        font-size: .8rem;
        line-height: 25px;
    }
}
.common-section {
    background: url(/NFTWebsite/static/media/bg.d631560b.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
}

.all__category__filter select ,.all__items__filter select , .filter___right select {
    padding: 7px 25px;
    font-size: .9rem;
    color: #fff;
    cursor: pointer;
    border: none;
    background: #343444;
    border-radius: 5px;
}


@media only screen and (max-width:768px) {
    .all__category__filter select ,.all__items__filter select , .filter___right select {
        padding: 5px 20px;
        font-size: .7rem;
    }
}




.form__input input, .form__input textarea {
    width: 100%;
    padding: 7px 25px;
    color: #fff;
    font-size: 0.8rem;
    background: transparent;
    border: 1px solid rgba(221, 221, 221, 0.171);
    outline: none;
    border-radius: 5px;
}

.form__input {
    margin-bottom: 15px;

}

.form__input label {
    color: #fff;
    margin-bottom: 10px;

}

.form__input .upload__input {
    padding: 30px 25px;
}

::-webkit-calendar-picker-indicator {
    filter: invert(1);
    cursor: pointer;
}

@media only screen and (maxi-width:576px) {
    .create__item {
        margin-top: 15px;
    }
}



.wallet__item {
    background: #343444;
    padding: 15px;
    text-align: center;
    border-radius: 5px;
    cursor: pointer;
}

.wallet__item span {
    width: 50px;
    height: 50px;
    line-height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    display: block;
    margin: auto;
}

.wallet__item span i {
    font-size: 2.5rem;
}

.wallet__item h5 {
    color: #fff !important;
    margin-top: 15px;
}

.wallet__item p {
    font-size: .8rem;
    line-height: 28px;
}




.single__nft-img {
    border-radius: 8px;

}

.single__nft-seen span i {
    color: #fff;
    padding: 5px;
    background: #000;
    border-radius: 50px;
    cursor: pointer;

}

.single__nft-seen span, .single__nft-more span {
    color: #fff;
    font-size: 0.8rem;

}

.single__nft-more span i {
    color: #fff;
    background: #343444;
    padding: 5px;
    border-radius: 50px;
}

.nft__creator {
    background: #343444;
    padding: 15px;
    border-radius: 7px;
    width: 50%;

}

.creator__img {
    width: 40px;
    height: 40px;

}

.creatot__detail p{
    font-size: .7rem;
    font-weight: 300;
    margin-bottom: 0;
    margin-top: 0;
}

.creatot__detail h6 {
    color: #fff;
    margin-bottom: 0;
    font-size: 0.9rem;
    font-weight: 500;
}

.singleNft-btn {
    padding: 8px 25px;
    background: #5142FC;
    border: none;
    outline: none;
    border-radius: 50px;
    text-align: center;
    transition: 0.3s;
}

.singleNft-btn a {
    text-decoration: none;
    color: #fff;
    font-size: 1rem;
}

.singleNft-btn:hover {
    background: #fff;
}

.singleNft-btn:hover a, .singleNft-btn:hover i {
    color: #5142FC;

}

.singleNft-btn i {
    color: #fff;
}

@media only screen and (maxi-width: 992px) {
    .single__nft__content h2 {
        font-size: 1.3rem;

    }

    .single__nft__content p {
        font-size: 0.8rem;
        line-height: 28px;

    }

