    padding: 8px 25px;
    margin-top: 20px;
    border: none;
    outline: none;
    font-size: 0.8rem;
    background: #343444;
}

:-ms-input-placeholder {
    color: #fff;
    opacity: 0.8;
}

::placeholder {
    color: #fff;
